import { RouterModule, Routes } from "@angular/router";

import { TODAY_ROUTES } from './today.routing';
import { CATEGORY_ROUTES } from './category.routing';

import { TodayGankComponent, CategoryComponent } from './components';

const APP_ROUTES: Routes = [
    { path: '', redirectTo: '/today', pathMatch: 'full' },
    { path: 'today', component: TodayGankComponent },
    { path: 'today', component: TodayGankComponent, children: TODAY_ROUTES },
    { path: 'category', component: CategoryComponent },
    { path: 'category', component: CategoryComponent, children: CATEGORY_ROUTES }

];

export const APP_ROUTING = RouterModule.forRoot(APP_ROUTES);
